<template>
  <div>
    <Header :text="title"></Header>
    <div class="container">
      <div class="section" v-for="(item) in listData" :key="item.secondName">
        <div class="sectionName">{{item.secondName}}</div>
        <div class="row" v-for="(info) in item.thirdLevel" :key="info.id">
          <div class="row-wrap" @click="toInfo(info.id,info.name)">
            <span class="wrap-text">{{info.name}}</span>
            <img data-v-69e513b9="" src="data:image/png;base64,">
          </div>
        </div>
        <div class="section-footer" v-if='!showStore' @click="toStoreHelp(item)">
          <div>
            前往{{item.secondName}}
          </div>
          <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/arrow-right.png">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import URL from '@/http/url'
import Header from '@/components/header'
// import utils from '@/utils/utils'
export default {
  components: {
    Header
  },
  data() {
    return {
      showStore: false,
      secondId: undefined,
      firstId: 1,
      listData: []
      // isHaierApp: false,
    }
  },
  created() {
    // this.isHaierApp = utils.isHaierApp();
    this.firstId = this.$route.query.firstId || 1
    if (this.$route.path === '/storeHelp') {
      this.showStore = true;
      this.secondId = this.$route.query.id
    }
    this.getList();
  },
  computed: {
    title() {
      return this.showStore && this.listData[0] ? this.listData[0].secondName : '帮助中心'
    }
  },
  watch: {
    '$route': function(newValue, oldValue) {
      this.firstId = this.$route.query.firstId || 1
      if (this.$route.path === '/storeHelp') {
        this.showStore = true;
        this.secondId = this.$route.query.id
      } else {
        this.showStore = false;
        this.secondId = undefined
      }
    }
  },
  methods: {
    toStoreHelp(item) {
      this.$router.push({ path: `/storeHelp?firstId=${this.firstId}&id=${item.secondId}&routekey=${Date.now()}` })
    },
    getList() {
      this.$http.get(URL.GET_HELP, {
        params: {
          leveOneId: this.firstId,
        }
      }).then(res => {
        if (res.data.success) {
          if (this.showStore && this.secondId) {
            this.listData = res.data.data.second.filter(i => {
              return Number(i.secondId) === Number(this.secondId)
            })
            if (this.listData[0]) {
              this.$util.gioTrackWithVariable('help_articleCate_view', {
                article_cate_id: (this.listData[0].secondId + '') || null,
                article_cate_name: this.listData[0].secondName || null,
              })
            }
          } else {
            this.listData = res.data.data.second
          }
        } else {
          this.$toast(res.data.message)
        }
      }).catch(e => {
        this.$toast(e.message)
      })
    },
    toInfo(id, title) {
      if (id) {
        this.$router.push({ path: 'documentInfo', query: { id, title } })
      } else {
        this.$toast('暂无数据')
      }
    }
  }
}
</script>
